<template>
  <div class="login-container">
    <!-- ËÉåÊôØÊ∏êÂèòÊïàÊûú -->
    <div class="background"></div>

    <!-- ÁôªÂΩïÂç°Áâá -->
    <el-card class="login-card">
      <h2 class="login-title">oci-helper Login</h2>
      <el-form @submit.prevent="login" @keydown.enter.prevent="login">
        <el-form-item label="" label-width="0%">
          <el-input v-model="loginParams.account" placeholder="ËØ∑ËæìÂÖ•Ë¥¶Âè∑" clearable>
            <template #prefix>
              <i class="el-icon-user"></i>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="" label-width="0%">
          <el-input show-password v-model="loginParams.password" placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å" type="password" clearable>
            <template #prefix>
              <i class="el-icon-lock"></i>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item v-if="enableMfa">
          <el-input show-password v-model="loginParams.mfaCode" placeholder="ËØ∑ËæìÂÖ•MFAÈ™åËØÅÁ†Å" clearable>
            <template #prefix>
              <i class="el-icon-mobile-phone"></i>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item style="text-align: center;" class="login-button-container">
          <el-button type="primary" @click="login" :loading="loading" class="login-button">Login</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- Footer -->
    <el-footer class="login-footer">
      <el-link href="https://github.com/Yohann0617" target="_blank">¬© Yohann All Rights Reserved</el-link>
      <el-button @click="toggleTheme" class="logout-button" link>{{ themeText }}</el-button>
    </el-footer>
  </div>
</template>

<script>
import {ElMessage} from "element-plus";

export default {
  mounted() {
    this.getEnableMfa();
    this.isDarkMode = localStorage.getItem('theme') === 'dark';
  },
  computed: {
    themeText() {
      return this.isDarkMode ? 'ÂºÄÁÅØüí°' : 'ÂÖ≥ÁÅØüåô';
    }
  },
  data() {
    return {
      loginParams: {
        account: '',
        password: '',
        mfaCode: null
      },
      loading: false,
      enableMfa: false,
      isDarkMode: localStorage.getItem('theme') === 'dark'
    };
  },
  methods: {
    toggleTheme() {
      const isDark = document.documentElement.classList.toggle('dark')
      localStorage.setItem('theme', isDark ? 'dark' : 'light')
      this.isDarkMode = isDark; // Êõ¥Êñ∞Áä∂ÊÄÅ
    },
    getEnableMfa() {
      this.$axios.post('/sys/getEnableMfa', {})
          .then(response => {
            if (response.data.success) {
              this.enableMfa = response.data.data;
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000
              });
            }
          })
          .catch(error => {
            console.error('Error:', error);
          });
    },
    async login() {
      if (this.loginParams.account === 'yohann' && this.loginParams.password === 'yohann') {
        ElMessage({
          message: 'ËØ•Ë¥¶Âè∑‰∏∫ÈªòËÆ§Ë¥¶Âè∑ÔºåËØ∑‰øÆÊîπÂπ∂ÈáçÂêØÂêéÂÜçÂ∞ùËØïÁôªÂΩï',
          type: 'error',
          duration: 2000
        });
        return;
      }

      this.loading = true;
      try {
        const response = await this.$axios.post('/sys/login', {...this.loginParams});
        if (response.data.success) {
          ElMessage({
            message: response.data.msg,
            type: 'success',
            duration: 2000
          });
          let loginRsp = response.data.data
          sessionStorage.setItem('token', loginRsp.token);
          localStorage.setItem('currentVersion', loginRsp.currentVersion);
          localStorage.setItem('latestVersion', loginRsp.latestVersion);
          setTimeout(() => {
            this.$router.push('/dashboard');
          }, 1000);
        } else {
          ElMessage({
            message: response.data.msg,
            type: 'error',
            duration: 2000
          });
          this.loading = false;
        }
      } catch (error) {
        console.error('Error:', error);
        ElMessage({
          message: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          type: 'error',
          duration: 2000
        });
        this.loading = false;
      }
    }
  }
};
</script>

<style scoped>
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #59A5F3FF, #ffffff);
  z-index: -1;
  animation: gradientAnimation 10s ease infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.login-card {
  width: 500px;
  padding: 20px;
  border-radius: 10px;
//box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); //background: rgba(255, 255, 255, 0.9); //animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

.login-title {
  text-align: center;
  margin-bottom: 20px; /* Èó¥Ë∑ùÂèòÂ∞è */
  color: #304156;
  font-size: 24px; /* Â≠ó‰ΩìÂèòÂ§ß */
}

.login-button-container {
  text-align: center;
}

.login-button {
  width: 100%;
  margin-top: 20px;
  background-color: #1E90FF; /* ËìùËâ≤ËÉåÊôØ */
  color: white; /* ÁôΩËâ≤ÊñáÂ≠ó */
  font-weight: bold;
  border-radius: 5px; /* ÂúÜËßíÂèòÂ∞è */
}

.login-footer {
  text-align: center;
  font-size: 14px;
  margin-top: 20px;
  color: #000; /* ÈªëËâ≤Â≠ó‰Ωì */
}
</style>
